* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.country-list {
  list-style: none;
  margin: 0;
  padding: 0;
}

.country-list__item {
  display: flex;
  align-items: center;
}

.country-list__name {
  font-size: 2em;
  font-weight: 700;
}

.country-list__img {
  display: inline-block;
  width: 2em;
  margin-right: 10px;
}

.country-list__label {
  font-weight: 700;
}